* {
  box-sizing: border-box;
  margin: 0;
}

body {
  color: #212121;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    padding: 20px 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

img {
  display: block;
}

.warning {
  display: none;
}

.header {
  background-color: #f7f7f7;
}

.index-header {
  width: 100%;
  height: 230px;
  background: url("background-home.564ae802.jpg") center / cover no-repeat;
}

.library-header {
  width: 100%;
  max-width: 1600px;
  height: 230px;
  background: url("background-library.fa153e04.jpg") center / cover no-repeat;
}

.header__box {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__box {
    justify-content: space-between;
    align-items: baseline;
  }
}

.header__svg {
  fill: #fff;
  border-radius: 5px;
}

.header__wrapper {
  flex-flow: row;
  align-items: baseline;
  gap: 8px;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .header__wrapper {
    gap: 10px;
  }
}

.logo {
  display: none;
}

@media screen and (min-width: 768px) {
  .logo {
    color: #fff;
    font-size: 30px;
    text-decoration: none;
    display: block;
  }
}

.nav-item {
  color: #fff;
  letter-spacing: .02em;
  line-height: 1.1;
  text-decoration: none;
}

.header__nav {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  display: flex;
}

.header__underline {
  position: relative;
}

.header__underline:after {
  content: "";
  height: 3px;
  background-color: #ff001b;
  border-radius: 2px;
  position: absolute;
  top: 15px;
  left: 0;
  right: 0;
}

.wrapper-form {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.search-form {
  width: 100%;
  padding-top: 40px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .search-form {
    max-width: 390px;
    justify-content: center;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .search-form {
    max-width: 450px;
  }
}

input {
  width: 100%;
  height: 30px;
  color: #fff;
  background: none;
  border-bottom: .5px solid #fff;
}

.search__svg {
  fill: #fff;
  position: absolute;
  bottom: 6px;
  right: 12px;
}

.buttons {
  justify-content: center;
  gap: 20px;
  padding-top: 40px;
  display: flex;
}

.button {
  color: #fff;
  cursor: pointer;
  background: none;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 14px 35px;
}

.button:hover, button:focus {
  color: #fff;
  background-color: #ff6b08;
  border: none;
  box-shadow: 0 3px 1px #0000001a, 0 1px 2px #00000014, 0 2px 2px #0000001f;
}

.gallery {
  margin: 0;
  padding: 0;
}

.gallery__list {
  width: 100%;
  background-image: repeating-linear-gradient(to right, #f7f7f7, #f7f7f7 130px, #fff 130px 150px);
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

@media screen and (min-width: 768px) {
  .gallery__list {
    gap: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .gallery__list {
    gap: 16px;
  }
}

.gallery__list-item {
  height: 443px;
  width: 280px;
  border-radius: 5px;
  margin: 0;
}

@media screen and (min-width: 768px) {
  .gallery__list-item {
    height: 494px;
    width: 336px;
  }
}

@media screen and (min-width: 1280px) {
  .gallery__list-item {
    height: 634px;
    width: 394px;
  }
}

.gallery__list-item > img {
  cursor: pointer;
  height: 402px;
  width: 280px;
  object-fit: cover;
  border-radius: 5px;
  display: block;
}

@media screen and (min-width: 768px) {
  .gallery__list-item > img {
    width: 336px;
    height: 455px;
  }
}

@media screen and (min-width: 1280px) {
  .gallery__list-item > img {
    width: 395px;
    height: 574px;
  }
}

.gallery__list-item > h3 {
  margin-top: 10px;
  font-size: 12px;
  line-height: 16px;
}

.gallery__list-item > p {
  color: #ff6b08;
  font-size: 12px;
  line-height: 16px;
}

footer {
  text-align: center;
  color: #545454;
  background-color: #f7f7f7;
  font-size: 14px;
  font-weight: 400;
}

@media screen and (min-width: 1280px) {
  footer {
    font-size: 16px;
  }
}

.footer-text {
  color: #545454;
}

